<template>
  <div class="sliderScroll">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="iframeBox">
          <iframe src="#/sliderScrollExample"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">sliderScroll 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import sliderScroll from "cy_static/sliderScroll"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
  &lt;style type="text/css"&gt;
    #myWin{width:375px;position:relative;height:300px;overflow:hidden;background-color:#e0eeee}
    #myWin .content{width:100%;padding: 0 178px;}
  &lt;/style&gt;

  &lt;div id="myWin"&gt;
      &lt;div class="content"&gt;
        中国人 
        五千年的风和雨啊 藏了多少梦
        黄色的脸 黑色的眼  不变是笑容
        八千里山川河岳  像是一首歌
      &lt;/div&gt;
  &lt;/div&gt;

  &lt;script type="text/javascript"&gt;
      import sliderScroll from "cy_static/sliderScroll"; 
      sliderScroll({
        hasMask : true,
        elem : "#myWin", // 窗口元素--要设置高度
        content : ".content"
      });
  &lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">事件</h2>
        <ul class="ul-props">
					<li>
						<p><a>scroll</a>() : <a>Void</a></p>
						<p>触发绑定在content滚动事件；</p>
					</li>
				</ul>
      </div>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>sliderScroll</a>(params : Object) : <a>Object</a></p>
						<p>上下滑动窗口，暂停(锁定), 恢复正常，滑动到固定位置等；</p>
            <table class="table-props">
                <tr>
                  <th>参数名</th>
                  <th>类型</th>
                  <th>描述</th>
                  <th>默认值</th>
                  <th class="text-c">必需</th>
                </tr>
                <tr>
                  <td>elem</td>
                  <td class="atn">String||Object</td>
                  <td>窗口元素</td>
                  <td>""</td>
                  <td class="text-c">Y</td>
                </tr>
                <tr>
                  <td>content</td>
                  <td class="atn">String||Object</td>
                  <td>窗口子元素</td>
                  <td>""</td>
                  <td class="text-c">Y</td>
                </tr>
                <tr>
                  <td>time</td>
                  <td class="atn">Number</td>
                  <td>滑动时间(毫秒)</td>
                  <td>600</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>drag</td>
                  <td class="atn">Number</td>
                  <td>阻尔糸数</td>
                  <td>0.9</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>springDrag</td>
                  <td class="atn">Number</td>
                  <td>超出窗体阻尔糸数</td>
                  <td>0.85</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>scrollTop</td>
                  <td class="atn">Number</td>
                  <td>默认滚动条位置</td>
                  <td>0</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>itemHeight</td>
                  <td class="atn">Number</td>
                  <td>整格滑动的高度</td>
                  <td>0</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>hasMask</td>
                  <td class="atn">Boolean</td>
                  <td>显示拉动弹性蒙层</td>
                  <td>false</td>
                  <td class="text-c">N</td>
                </tr>                
                <tr>
                  <td>isSpring</td>
                  <td class="atn">Boolean</td>
                  <td>启动弹性吸附效果</td>
                  <td>true</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>isTopEffect</td>
                  <td class="atn">Boolean</td>
                  <td>顶部动画效果</td>
                  <td>false</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>isBotEffect</td>
                  <td class="atn">Boolean</td>
                  <td>底部动画效果</td>
                  <td>true</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>hasScrollbar</td>
                  <td class="atn">Boolean</td>
                  <td>显示滚动条</td>
                  <td>true</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>isLockfull</td>
                  <td class="atn">Boolean</td>
                  <td>锁定全屏状态(取消系统默认滚动)</td>
                  <td>false</td>
                  <td class="text-c">N</td>
                </tr> 
                <tr>
                  <td>isLockTopSpace</td>
                  <td class="atn">Boolean</td>
                  <td>锁定必须滚动top到顶再释放默认事件</td>
                  <td>false</td>
                  <td class="text-c">N</td>
                </tr> 
                <tr>
                  <td>isLockBotSpace</td>
                  <td class="atn">Boolean</td>
                  <td>锁定必须滚动bottom到底再释放默认事件</td>
                  <td>false</td>
                  <td class="text-c">N</td>
                </tr> 
                <tr>
                  <td>hasHoveScrollbar</td>
                  <td class="atn">Boolean</td>
                  <td>PC端hove时显示滚动条</td>
                  <td>false</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>pullDown</td>
                  <td class="atn">Object</td>
                  <td>下拉刷新(顶部刷新加载)对象{}</td>
                  <td>null</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>pushDown</td>
                  <td class="atn">Object</td>
                  <td>上拉刷新(底部刷新加载)对象{}</td>
                  <td>null</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onOverflow</td>
                  <td class="atn">Function</td>
                  <td>滑动超出窗口高度</td>
                  <td>function (height) {}</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onTopOrBottom</td>
                  <td class="atn">Function</td>
                  <td>已到顶部OR底部</td>
                  <td>function (height, event, params) {}</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onStart</td>
                  <td class="atn">Function</td>
                  <td>拖动开始</td>
                  <td>function (height, event, params) {}</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onScroll</td>
                  <td class="atn">Function</td>
                  <td>滚动事件</td>
                  <td>function (height) {}</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onMove</td>
                  <td class="atn">Function</td>
                  <td>拖动中</td>
                  <td>function (height, event, params) {}</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onStop</td>
                  <td class="atn">Function</td>
                  <td>拖动结束</td>
                  <td>function (height, event, params) {}</td>
                  <td class="text-c">N</td>
                </tr>
            </table>
      </div>

      <div class="apiModule">
        <h4 class="cy_h4">pullDown下拉刷新(顶部刷新加载)对象属性</h4>
        <table class="table-props">
          <tr>
            <th>参数名</th>
            <th>类型</th>
            <th>描述</th>
            <th>默认值</th>
            <th class="text-c">必需</th>
          </tr>
          <tr>
            <td>onShowCss</td>
            <td class="atn">String</td>
            <td>"onShow"</td>
            <td>默认样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onReadCss</td>
            <td class="atn">String</td>
            <td>"onRead"</td>
            <td>超出拖动距离样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onLoadCss</td>
            <td class="atn">String</td>
            <td>"onLoad"</td>
            <td>加载中的样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onEndCss</td>
            <td class="atn">String</td>
            <td>"onEnd"</td>
            <td>加载结束样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onShow</td>
            <td class="atn">String</td>
            <td>"上拉可以刷新"</td>
            <td>在下拉可刷新状态时，下拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onRead</td>
            <td class="atn">String</td>
            <td>"释放立即刷新"</td>
            <td>在释放可刷新状态时，下拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onLoad</td>
            <td class="atn">String</td>
            <td>"正在加载..."</td>
            <td>在释放可刷新状态时，下拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onEnd</td>
            <td class="atn">String</td>
            <td>"已经到底啦!"</td>
            <td>正在刷新状态时，下拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>callback</td>
            <td class="atn">Function</td>
            <td>function(params, content) {}</td>
            <td>加载数据</td>
            <td class="text-c">N</td>
          </tr>
        </table>
        <div class="br"></div>
        <h4 class="cy_h4">pushDown上拉刷新(顶部刷新加载)对象属性</h4>
        <table class="table-props">
          <tr>
            <th>参数名</th>
            <th>类型</th>
            <th>描述</th>
            <th>默认值</th>
            <th class="text-c">必需</th>
          </tr>
          <tr>
            <td>onShowCss</td>
            <td class="atn">String</td>
            <td>"onShow"</td>
            <td>默认样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onReadCss</td>
            <td class="atn">String</td>
            <td>"onRead"</td>
            <td>超出拖动距离样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onLoadCss</td>
            <td class="atn">String</td>
            <td>"onLoad"</td>
            <td>加载中的样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onEndCss</td>
            <td class="atn">String</td>
            <td>"onEnd"</td>
            <td>加载结束样式</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onShow</td>
            <td class="atn">String</td>
            <td>"上拉可以刷新"</td>
            <td>可选，在上拉可刷新状态时，上拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onRead</td>
            <td class="atn">String</td>
            <td>"释放立即刷新"</td>
            <td>可选，在释放可刷新状态时，上拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onLoad</td>
            <td class="atn">String</td>
            <td>"正在加载..."</td>
            <td>可选，在释放可刷新状态时，上拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>onEnd</td>
            <td class="atn">String</td>
            <td>"已经到底啦!"</td>
            <td>可选，正在刷新状态时，上拉刷新控件上显示的标题内容</td>
            <td class="text-c">N</td>
          </tr>
          <tr>
            <td>callback</td>
            <td class="atn">String</td>
            <td>function(params, content) {}</td>
            <td>加载数据</td>
            <td class="text-c">N</td>
          </tr>
        </table>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">窗口事件</h2>
        <ul class="ul-props">
					<li>
						<p><a>scroll</a>() : <a>Void</a></p>
						<p>触发绑定在content滚动事件；</p>
					</li>
        </ul>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>pause</a>() : <a>Void</a></p>
						<p>禁止滑动；</p>
					</li>
					<li>
						<p><a>play</a>() : <a>Void</a></p>
						<p>启动滑动；</p>
					</li>
					<li>
						<p><a>scrollTop</a>(index : Number) : <a>Void</a></p>
						<p>把滚动条滑动到固定位置；</p>
					</li>
					<li>
						<p><a>setLockfull</a>(isLock : Boolean) : <a>Void</a></p>
						<p>设置全屏锁定状态；</p>
					</li>
					<li>
						<p><a>setLockTopSpace</a>(isLock: Boolean) : <a>Void</a></p>
						<p>锁定必须滚动top到顶再释放默认事件；</p>
					</li>
					<li>
						<p><a>setLockBotSpace</a>(isLockf : Boolean) : <a>Void</a></p>
						<p>锁定必须滚动bottom到底再释放默认事件；</p>
					</li>		
					<li>
						<p><a>setActivated</a>(isActivated) : <a>Void</a></p>
						<p>是否激活；</p>
					</li>		
					<li>
						<p><a>setItemHeight</a>(itemHeight) : <a>Void</a></p>
						<p>设置整格移动的高度；</p>
					</li>
					<li>
						<p><a>destroy</a>() : <a>Void</a></p>
						<p>销毁实例里所有事件；</p>
					</li>									
					<li>
						<p><a>isEndPull</a>(isEnd : Boolean) : <a>Void</a></p>
						<p>下拉滚动加载异步数据时，用于设置滚动内容加载状态(加载完还是还有下一页)超时15秒自动设置为还有下一页</p>
					</li>									
					<li>
						<p><a>isEndPush</a>(isEnd : Boolean) : <a>Void</a></p>
						<p>上拉滚动加载异步数据时，用于设置滚动内容加载状态(加载完还是还有下一页)超时15秒自动设置为还有下一页</p>
					</li>
				</ul>        
      </div>




    </div> 
  </div>
</template>

<script>
export default {
  name: 'sliderScroll',
  data () {
    return {
      msg: 'sliderScroll',
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;
    m.$refs.codeStyle1.codemirror.setValue(m.$refs.codeStyleVal1.innerHTML.replace(/^\s|\s+$/g,''));
    m.$refs.codeStyle2.codemirror.setValue(m.$refs.codeStyleVal2.innerHTML.replace(/^\s|\s+$/g,''));
  }
}
</script>    
<style scoped>
  .cy_example .iframeBox{position:absolute;top:43px;width:100%;bottom:0}
</style>


